@import url(https://fonts.googleapis.com/css2?family=Fjalla+One&family=Titillium+Web:wght@300;400;600&display=swap);
:root {
  --overlay-gradient: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.7));
  --color-1: #032535;
  --color-2: #ff9a00;
  --color-2-dark: #ff5700;
  --color-3: #fff;
  --color-4: #66fcf1;
  --color-5: #45a29e;
  --font-headings: 'Fjalla One', sans-serif;
  --font-body: 'Titillium Web', sans-serif;
}

/*  INITIAL SETUPS */
/* font weight for body: 300 400 600 */
#root {
  position: relative;
  margin: 0 auto;
}
*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}
html {
  font-size: 62.5%;
  font-family: 'Titillium Web', sans-serif;
  font-family: var(--font-body);
  color: #fff;
  color: var(--color-3);
  /* 1rem=10px */
  background: #e5e5e5;
  overflow-x: hidden;
}

body {
  box-sizing: border-box;
  font-weight: 300;
  font-size: 1.6rem;
  width: 140rem;
  margin: 0 auto;
}

/*  STYLE HEADINGS */

h2,
h3 {
  font-family: 'Fjalla One', sans-serif;
  font-family: var(--font-headings);
}
h2 {
  font-size: 350%;
  color: #1d222b;
  text-align: center;
  letter-spacing: 0.1rem;
}
h3 {
  font-size: 250%;
}

/* STYLING FLEX CONTAINERS */
.row {
  width: 114rem;
  margin: 0 auto;
}
.f-mid-col,
.f-mid-row {
  display: flex;
  align-items: center;
  justify-content: center;
}
.f-mid-col {
  flex-direction: column;
}
.f-mid-row {
  flex-direction: row;
}

a {
  text-decoration: none;
  color: unset;
}

/*  STYLING BUTTONS */
.btn {
  position: relative;
  padding: 1.4rem 4.2rem;
  padding-right: 3.1rem;
  font-size: 1.4rem;
  color: #ff9a00;
  color: var(--color-2);
  letter-spacing: 0.5rem;
  text-transform: uppercase;
  transition: all 500ms cubic-bezier(0.77, 0, 0.175, 1);
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  z-index: 1;
}

.btn:before,
.btn:after {
  content: '';
  position: absolute;
  transition: inherit;
  z-index: -1;
}

.btn:hover {
  color: #fff;
  color: var(--color-3);
  transition-delay: 0.5s;
}

.btn:hover:before {
  transition-delay: 0s;
}

.btn:hover:after {
  background: #ff9a00;
  background: var(--color-2);
  transition-delay: 0.35s;
}

/* From Top */

.from-top:before,
.from-top:after {
  left: 0;
  height: 0;
  width: 100%;
}

.from-top:before {
  bottom: 0;
  border: 1px solid #ff9a00;
  border: 1px solid var(--color-2);
  border-top: 0;
  border-bottom: 0;
}

.from-top:after {
  top: 0;
  height: 0;
}

.from-top:hover:before,
.from-top:hover:after {
  height: 100%;
}
.btn--orange::before {
  width: 100%;
  height: 100%;
  border: 1px red solid;
}
.btn--orange:hover::before {
  border: none;
  transform: translateY(-1px);
}

/* From Left */

.from-left:before,
.from-left:after {
  top: 0;
  width: 0;
  height: 100%;
}

.from-left:before {
  right: 0;
  border: 1px solid var(--inv);
  border-left: 0;
  border-right: 0;
}

.from-left:after {
  left: 0;
}

.from-left:hover:before,
.from-left:hover:after {
  width: 100%;
}

.cards {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.col-1-of-3 {
  width: 40rem;
}

.spacer {
  flex: 1 1;
}


header {
  height: 100vh;
  background: var(--overlay-gradient), url(/portfolio/static/media/hero2.3f57f32a.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
h1 {
  width: 100%;
  line-height: 1.4;
  font-family: var(--font-headings);
}
.h1--main {
  font-size: 400%;
}
.h1--sub {
  font-size: 100%;
  align-self: center;
  text-align: center;
  width: 75%;
  color: var(--color-3);
  line-height: 1.2;
  text-align: end;
}
.h1__photo {
  background-image: url(/portfolio/static/media/my-beautiful-awesome-face.8bce280a.jpg);
  width: 14rem;
  height: 14rem;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: center;
  background-size: contain;
  border-radius: 10rem;
  margin-left: 4rem;
}
.h1__name-greetings {
  font-size: 200%;
}
.h1__jobs__list {
  font-family: var(--font-body);
  font-size: 55%;
}

.header__cta {
  margin-top: 6rem;
  margin-left: 1rem;
}

#portfolio {
  /* height: 100vh; */
  padding: 6rem 0 9rem 0;
  background-color: white;
}
#portfolio h2 {
  margin-bottom: 2rem;
}
.cards {
  display: flex;
  justify-content: space-between;
}

.card:hover {
  box-shadow: 0px 0px 12px 0px rgba(168, 153, 168, 1);
}
.card:not(:last-child) {
  margin-bottom: 3rem;
}

.bullet-tag {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 30px;
  background: #f0f4f9;
  padding: 0.5rem 1rem;
  width: inherit;
  margin: 0.6rem 0.3rem;
}
.bullet-tag__text {
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  color: #1d222b;
}
.bullet__ellipse {
  height: 0.8rem;
  width: 0.8rem;
  border-radius: 50%;
  background-color: black;

  margin-right: 0.4rem;
}
.bullet__ellipse--blue {
  background-color: #468fa6;
}
.bullet__ellipse--yellow {
  background-color: #f2bb13;
}
.bullet__ellipse--reddish {
  background-color: #e44d26;
}
.bullet__ellipse--blue-1 {
  background-color: #264de4;
}
.bullet__ellipse--green {
  background-color: #3d7b28;
}

.card {
  display: flex;
  width: 110rem;
  border-radius: 1.6rem;
  border: 1px solid #e4e9ed;
  flex-direction: row;
  justify-items: center;
}
.card__image {
  width: 45rem;
  height: 25rem;
  margin: 1.2rem;
  display: inline-flex;
}
.card__jpg {
  border-radius: 1.6rem;
  height: 100%;
  width: -webkit-fill-available;
}
.card__text {
  margin: 2.1rem;

  margin-top: 2.5rem;
  width: 70%;
  color: #1d222b;
  display: flex;
  flex-direction: column;
}
.card__title {
  font-weight: 500;
}
.card__description {
  padding-top: 1.2rem;
  color: #8a9099;
}

#contact {
  height: 100vh;
  background: url(/portfolio/static/media/footer.8ffc6e2d.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-form {
  width: 60rem;
  background-color: #fff;
  border-radius: 0.5rem;
  box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28);
  padding: 0.5rem;
}
textarea {
  width: 100%;
  resize: none;
  outline: 0;
  padding: 1rem 2rem;
  height: 20rem;
  font-style: italic;
  border: none;
}

.form-actions {
  display: flex;
  justify-content: flex-end;
}

.input_form {
  height: auto;
  display: flex;
  flex-direction: column;
}

.input-form__user-details {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.input-form__user-details .input-text:first-child {
  margin-right: 1rem;
}
.input-text {
  /* box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28); */

  width: 100%;
  height: 6rem;
  position: relative;
  overflow: hidden;
}

.input-text input {
  outline: none;
  width: 100%;
  font-size: 2rem;
  height: 100%;
  padding-left: 2rem;
  border: none;
}
.input-text label {
  position: absolute;
  left: 0;
  border-bottom: 1px black solid;
  bottom: 0;
  height: 100%;
  color: #1d222b;
  width: 100%;
  pointer-events: none;
}
.input-text input[type='text'] {
  background-color: unset;
}

.input-text label::after {
  content: '';
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  bottom: -1px;
  border-bottom: 4px var(--color-5) solid;
  transform: translateX(-100%);
  transition: transform 0.3s ease;
}
.input-text__title {
  position: absolute;
  bottom: 5px;
  left: 0px;
  transition: all 0.3s ease;
}
.input-text input:focus + .input-text__label .input-text__title,
.input-text input:valid + .input-text__label .input-text__title {
  transform: translateY(-150%);
  font-size: 1.4rem;
  color: var(--color-5);
}

.input-text input:focus + .input-text__label::after,
.input-text input:valid + .input-text__label::after {
  transform: translateX(0);
}
.input-form__article {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.btns-btn {
  background-color: unset;
  padding: 0.5rem 0.7rem;
  border-radius: 0.2rem;
  /* outline: 0; */
  border: 1px black solid;
  letter-spacing: 0.5px;
  cursor: pointer;
  display: flex;
  align-items: center;
  box-shadow: 0 2px 6px rgba(32, 33, 36, 0.28);
  transform: scale(1);
  transition: all 0.3s ease;
}
.btn-send {
  border: 1px var(--color-4) solid;
}
.btns-btn:active,
.btns-btn:focus {
  border: 1px green solid;
  outline: 0;
}
.btn-svg {
  padding: 0 0 0 1.2rem;
  height: 100% !important;
  display: flex;
  align-items: center;
}
.btns-btn:hover .btn-svg {
  transform: scale(1.03);
  fill-opacity: 0.9;
}

.btns-btn:active {
  transform: scale(1.05);
  outline: 0;
}

/* .svg-icon:hover {
  background-color: #292;
  border-radius: 100px;
} */

.nav {
  position: absolute;
  top: 0;
  width: 100%;

  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 8rem;
  /* background-color: rgba(0, 0, 0, 0.2); */
}

.sticky {
  background: rgba(0, 0, 0, 0.63);
  height: 6rem !important;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 9999;
}
.nav__toggler {
  display: none;
}
.nav__items {
  overflow: hidden;
  list-style: none;

  height: 100%;
}

.nav__item {
  height: 100%;
  padding: 0 2rem;
  background-color: unset;
}

.nav__item {
  position: relative;
  text-decoration: none;
  color: white;
  font-weight: 400;
  z-index: 1;
  font-weight: 1.4rem;
}

.nav__item--active {
  font-weight: 600;
  color: var(--color-2);
  letter-spacing: 0.3rem;
}
.nav__item a:hover {
  color: var(--color-2-dark);
}

@-webkit-keyframes fillUp {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes fillUp {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0);
  }
}

.burger {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
  border: 1px var(--color-3) solid;
  padding: 0.3rem;
  border-radius: 0.3rem;
  overflow: hidden;
}
.bar-1,
.bar-2,
.bar-3 {
  width: 100%;
  height: 0.4rem;
  background-color: var(--color-3);
}
.burger--open {
  position: relative;
  border: 1px var(--color-2) solid;
  padding: 0.3rem;
  border-radius: 0.3rem;
}

.burger--open .bar-2 {
  width: 85%;
  transition: all 0.5s ease;
  justify-self: left;
}
.burger--open .bar-3 {
  width: 65%;
  transition: all 0.5s ease;
}

.footer {
  height: 12rem;
  display: flex;
  flex-direction: column;
  background-color: white;
  justify-content: center;
  align-items: center;
  color: #1d222b;
}
.footer__nav {
  display: flex;
  justify-content: space-between;
}

.footer__links {
  list-style: none;
  display: flex;
  color: #5f6773;
}

.footer__link:not(:last-child) {
  padding-right: 1.5rem;
}
.icon-wrapper,
.icon-wrapper a {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  height: 3rem;
  width: 3rem;
  background-color: rgba(134, 86, 86, 0.525);
  transition: all 0.3s ease;
  cursor: pointer;
}
.footer__social {
  display: flex;
  justify-content: center;
  padding-top: 1rem;
}

.footer__social .icon-wrapper:not(:last-child) {
  margin-right: 0.5rem;
}
.icon-wrapper:hover {
  background-color: rgba(223, 161, 47, 0.525);
}

